body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'DM Mono', monospace;
}

.mediaQuery-wrapper{
  display: none;
}

.Layout {
  height: 100vh;
  width: 100vw;
  background: #333;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 1300px) {
  .mediaQuery-wrapper {
    width: 100vw;
    height: 100vh;
    background: #333;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .Layout{
      display: none;
  }

  .mediaQuery-message {
      display: inherit;
      font-size: 2.5rem;
      color: white;
  }

}